@import 'variables.css';

.home-headline.outer {
    background-color: color(var(--darkgrey));
    color: #fff;
    text-align: center;
    padding-top: 6em;
}

.home-headline h1 {
    margin: 0;
}

.home-call-to-action {
    margin-top: 3em;
}

.home-call-to-action-alternative {
    margin-top: 1em;
    color: #999;
}

.home-call-to-action a {
    color: #fff;
    margin-bottom: 1em;
}

.home-laptop {
    margin-top: 6em;
    margin-bottom: -3em;
}

.home-socialproof img {
    filter: grayscale(1);
    height: 60px;
    margin-left: 4em;
    opacity: 0.5;
}

.home-socialproof.outer {
    padding-top: 6em;
    padding-bottom: 3em;
    text-align: center;
    color: #8D8F90;
}

.home-section.outer {
    padding-top: 6em;
    padding-bottom: 6em;
    font-size: 18px;
}


.home-section h2 {
    font-family: 'Fjalla One', sans-serif;
    font-size: 5rem;
    text-align: center;
    margin-bottom: 0px;
    margin-top: 0px;
    color: #000;
}

.title-description {
    padding: 5px 0;
    font-size: 2.2rem;
    font-weight: 300;
    letter-spacing: .5px;
    opacity: .8;
    text-align: center;
    margin-bottom: 3em;
}

.home-principle {
    float: left;
    width: 25%;
    box-sizing: border-box;
    padding: 1em;
    vertical-align: top;
    
}

.home-principle p {
    text-align: center;
    margin-bottom: 0;
}

.home-principle h3 {
    text-align: center;
    color: #000;
    font-family: "Fjalla One", sans-serif;
    font-size: 1.5em;
    min-height: 60px;
    margin-top: 0;
}

.home-section.is-blue {
    background-color: var(--blue);
    background: repeating-linear-gradient(65deg, var(--darkblue), var(--darkblue) 20%, var(--blue) 20%, var(--blue) 80%);

    color: #fff;
}

.home-section.is-grey {
    background-color: #eee;
}

.is-blue h2 {
    color: #fff;
}

.home-secondary-call-to-action {
    text-align: center;
}

.testimonial-inner {
    border-radius: 4px;
    padding: 2em;
    background-color: #eee;
    margin: 0em 1em;
}

.testimonial-inner p {
    font-size: 1.5em;
    line-height: 1.5em;
    font-style: italic;
}

.testimonial-author, 
.testimonial-author-title {
    color: #999;
}

.is-centered {
    text-align: center;
}

@media only screen and (max-width: 768px) {
    .home-socialproof img {
        margin: 2em 2em 0 2em;
    }

    .home-principle h3 {
        min-height: auto;
    }

}

.is-text-blue {
    color: var(--blue);
}